import React, { Component } from 'react'
import { Input, Button, Card, Form,InputNumber  } from 'antd'
import 'antd/dist/antd.css'

class Comp_regit extends Component {

    Regit=(e)=>{
        console.log(e)
    }
    render() {
        return (
            <div>
                <Card>
                    <Form layout="vertical" onClick={this.Regit}>
                        <Form.Item label={"E-mail"}>
                            <Input type={"email"} />
                        </Form.Item>
                        <Form.Item label={"Password"}>
                            <Input.Password />
                        </Form.Item>
                        <Form.Item label={"Confirm Password"}>
                            <Input.Password />
                        </Form.Item>
                        <Form.Item label={"ชื่อร้านค้า"}>
                            <Input />
                        </Form.Item>
                        <Form.Item label={"เบอร์โทร"}>
                            <Input/>
                        </Form.Item>
                        <Form.Item>
                            <Button htmlType="">Regiter</Button>
                        </Form.Item>
                    </Form>

                </Card>
            </div>
        )
    }
}
export default Comp_regit